<template>
<div class="wode">
  <!--背景-->
  <div class="bj">
    <div class="shezhi">
      <div class="left" @click="shezhi"><img src="/img/wode/jichu/wode-shezhi.png" alt="" /></div>
      <div class="right" @click="xinxi"><img src="/img/wode/jichu/wode-xiaoxi.png" alt="" /></div>
    </div>
  </div>
  <!--用户logo-->
  <div class="userLogo">
    <div class="sidai">
      <img src="/img/wode/jichu/wode-sidai.png" alt=""  v-show="logined"/>
      <div class="sidai-name"  v-show="logined">
        <div class="sidai-name-left">手记</div>
        <div class="sidai-name-right">收藏</div>
      </div>
      <div class="logo">
        <img :src="this.$store.state.user.touxiang" alt="" v-show="logined"/>
        <img src="/img/wode/nologin.png" alt="" v-show="!logined" style="margin-top: 28px;" @click="gologin()"/>
      </div>
    </div>
    <div class="myNumber">
      <div class="myNumber-left" v-show="logined">5</div>
      <div class="myNumber-right" v-show="logined">6</div>
    </div>
    <div class="myUsername"  v-show="logined">我的名字是什么</div>
  </div>
  <!--我的订单-->
  <div class="dingdan" v-show="logined">
    <ul class="dingdan-items">
      <li class="dingdan-item" v-for="(item,index) in dingdanData" :key="index" @click="tiaozhuan(index)">
        <div class="dingdan-item-div" v-if="index == 0"  @click="quanbudingdan">
          <div class="item-left item1-left">
            <div class="name-bold">{{ item.name }}</div>
            <div>{{ item.quanbu }}</div>
          </div>
          <div class="item-right">
            <img :src="item.lastImg" alt="" />
          </div>
        </div>
        <div v-else-if="index == 1">
          <ul class="kuaidi-items">
            <li class="kuaidi-item" v-for="(item2,index2) in item.kuaidi" :key="index2" @click="qitadd(index2)">
              <div style="margin-bottom:15px"><img :src="item2.img" alt="" /></div>
              <div>{{ item2.name }}</div>
              <div v-if="index2 == 0" class="daifukuan-numder yuan" v-show="daifukuan" v-text="daifukuanNumber"></div>
              <div v-else-if="index2 == 1" class="daifahuo-number yuan" v-show="daifahuo" v-text="daifahuoNumber"></div>
              <div v-else-if="index2 == 2" class="peisongzhong-number yuan" v-show="peisongzhong" v-text="peisongzhongNumber"></div>
              <div v-else class="daipingjia-number yuan" v-show="daipingjia" v-text="daipingjiaNumber"></div>
            </li>
          </ul>
        </div>
        <div class="dingdan-item-div" v-else>
          <div class="item-left">
            <div><img :src="item.img" alt="" /></div>
            <div class="name-bold">{{ item.name }}</div>
          </div>
          <div class="item-right">
            <img :src="item.lastImg" alt="" />
          </div>
        </div>
      </li>
    </ul>
  </div>
</div>
</template>

<script>
export default {
  name: "wode",
  data:function(){
    return {
      logined:false,
      dingdanData: [
        {
          name: "我的订单",
          quanbu: "全部订单",
          lastImg: "/img/wode/jichu/wode-back.png"
        },
        {
          kuaidi: [
            {
              img: "/img/wode/jichu/wode-daifukuan.png",
              name: "待付款"
            },
            {
              img: "/img/wode/jichu/wode-daifahuo.png",
              name: "待发货"
            },
            {
              img: "/img/wode/jichu/wode-peisongzhong.png",
              name: "配送中"
            },
            {
              img: "/img/wode/jichu/wode-daipingjia.png",
              name: "待评价"
            }
          ]
        },
        {
          img: "/img/wode/jichu/wode-youhuijuan.png",
          name: "优惠劵",
          lastImg: "/img/wode/jichu/wode-back.png"
        },
        {
          img: "/img/wode/jichu/wode-kefu.png",
          name: "客服",
          lastImg: "/img/wode/jichu/wode-back.png"
        },
        {
          img: "/img/wode/jichu/wode-guanzhu.png",
          name: "我的关注",
          lastImg: "/img/wode/jichu/wode-back.png"
        },
        {
          img: "/img/wode/jichu/wode-shouhuodizhi.png",
          name: "收货地址",
          lastImg: "/img/wode/jichu/wode-back.png"
        }
      ],
      daifukuan: false,
      daifahuo: false,
      peisongzhong: false,
      daipingjia: false,
      daifukuanNumber: 0,
      daifahuoNumber: 0,
      peisongzhongNumber: 0,
      daipingjiaNumber: 0
    }
  },
  methods: {
    gologin(){
      this.$router.push("/login")
    },
      tiaozhuan(a){
          switch (a) {
              case 2:
                  //优惠券
                  this.$router.push("/youhuiquan")
              break;
              case 3:
                  //客服
                  this.$router.push("/kefuxx")
                  break;
              case 4:
                  //我的关注
                  this.$router.push("/wodeguanzhu")
                  break;
              case 5:
                  //收货地址
                  this.$router.push("/shdz")
                  break;
          }
      },
    // 设置
    shezhi(){
      //跳转到设置页面
        this.$router.push("/shezhi")
    },
    // 信息
    xinxi(){
      //跳转到信息页面
        this.$router.push("/xiaoxi")
    },
    // 全部订单
    quanbudingdan(){
      //跳转到全部订单页面
      this.$router.push({
          name:"mydingdan",
          params:{
              num: 0
          }
      })
    },
      qitadd(a){
          a = a+1
          this.$router.push({
              name:"mydingdan",
              params:{
                  num: a
              }
          })
      },
    backGo(){
      this.$router.go(-1)
    }
  },
  // 生命周期钩子
  mounted(){
    if(this.$store.state.user.username != null){
      this.logined = true
    }
    this.$axios.get('/json/daifukuan.json').then(res => {
      console.log(res)
      var daifukuan = []
      var daifahuo = []
      var peisongzhong = []
      var daipingjia = []
      // 从数据库中获取到各个type的数据
      for (var i in res.data) {
        if(res.data[i].type == 0){
          daifukuan.push(res.data[i])
        }else if(res.data[i].type == 1){
          daifahuo.push(res.data[i])
        }else if(res.data[i].type == 2){
          peisongzhong.push(res.data[i])
        }else{
          daipingjia.push(res.data[i])
        }
      }
      console.log(daifukuan,daifahuo,peisongzhong,daipingjia)
      this.daifukuanNumber = daifukuan.length
      this.daifahuoNumber = daifahuo.length
      this.peisongzhongNumber = peisongzhong.length
      this.daipingjiaNumber = daipingjia.length
      // 判断待付款的数量
      if(this.daifukuanNumber > 0){
        this.daifukuan = true
      }else{
        this.daifukuan = false
      }
      // 判断待发货的数量
      if(this.daifahuoNumber > 0){
        this.daifahuo = true
      }else{
        this.daifahuo = false
      }
      // 判断配送中的数量
      if(this.peisongzhongNumber > 0){
        this.peisongzhong = true
      }else{
        this.peisongzhong = false
      }
      // 判断待评价的数量
      if(this.daipingjiaNumber > 0){
        this.daipingjia = true
      }else{
        this.daipingjia = false
      }
    }).catch(err => {
      console.log(err)
    })
  }
}
</script>

<style scoped>
.wode {
  width: 100%;
  height: 100%;
  background: url("/img/wode/jichu/wode-bj.png") no-repeat;
  background-size: 100% 400px;
  padding: 10px 30px;
  position: relative;
}
/*背景*/

.wode .bj {
  width: 100%;
  height: 350px;
}

.wode .shezhi {
  margin-top: 70px;
  display: flex;
  justify-content: space-between;
}
/*用户logo*/

.wode .userLogo {
  width: calc(100% - 60px);
  padding: 0 20px;
  height: 280px;
  background-color: white;
  border-radius: 10px;
  box-shadow:0 0 20px #ecf6de;
  position: absolute;
  top: 160px;
  text-align: center;
}

.wode .userLogo .sidai {
  margin-top: 80px;
  position: relative;
}

.wode .userLogo .sidai .sidai-name {
  width: 428px;
  height: 78px;
  display: flex;
  justify-content: space-between;
  position: absolute;
  left: calc(50% - 214px);
  top: 0;
  font-size: 22px;
  color: white;
  line-height: 78px;
  padding: 0 50px;
}

.wode .userLogo .logo {
  width:200px;
  height:200px;
  border-radius: 50%;
  background:white;
  box-shadow: 0 0 5px #C0C0C0;
  position: absolute;
  left: calc(50% - 100px);
  top: -60px;
  margin: auto;
  background: #e6e6e6;
}

.wode .userLogo .myNumber {
  width: 428px;
  height: 40px;
  padding: 0 60px;
  margin: auto;
  font-size: 18px;
  color: #333333;
  display: flex;
  justify-content: space-between;
}

.wode .userLogo .myUsername {
  margin-top: 35px;
  font-size: 22px;
  color: #98d15c;
}
/*我的订单*/
.wode .dingdan{
  width:100%;
  height:100%;
  padding:0 40px;
  font-size:20px;
  color:#333333;
  margin:80px 0 200px;
}
.wode .dingdan .dingdan-items>li{
  border-bottom: 1px solid #eeeeee;
  height: 100px;
  line-height: 60px;
}
.wode .dingdan .dingdan-items>li:first-child{
  border: none;
}
.wode .dingdan .dingdan-items>li:nth-child(2){
  line-height: normal;
  height: 120px;
}



.wode .dingdan .dingdan-item{
  padding:20px 0;
}
.wode .dingdan .name-bold{
  font-size: 30px;
  color: #1b1b1b;
}
.wode .dingdan .item-left>div:first-child>img{
  margin: 20px;
  position: relative;
  top: -6px;
}


.wode .dingdan .item-left>div:last-child{
  font-size: 28px;
  color: #515151;
}
.wode .dingdan .dingdan-item-div{
  width:100%;
  height:100%;
  display: flex;
  justify-content: space-between;
}
.wode .dingdan .dingdan-item-div .item-left{
  display: flex;
  justify-content: space-between;
}
.wode .dingdan .dingdan-item-div .item1-left{
  width:95%;
}
/*订单处理*/
.wode .dingdan .kuaidi-items{
  display: flex;
  justify-content: space-around;
  text-align: center;
}
.wode .dingdan .kuaidi-item{
  position:relative;
}
.wode .dingdan .kuaidi-items .yuan{
  width:20px;
  height:20px;
  border: 2px solid red;
  border-radius: 50%;
  font-size:14px;
  font-weight: bold;
  text-align: center;
  line-height:20px;
  color:red;
  position:absolute;
  left:50px;
  top:-8px;
}


  .kuaidi-item>div:nth-child(2){
    font-size: 28px;
    color: #515151;
  }
</style>
